import { name, version } from '../ds.plugin.config'

/**
 * Ds content plugin.
 * @module plugin
 */
export default {
  name,
  version,
  dependencies: [
    {
      name: 'dsUtilities',
      version: 1
    }
  ],
  data: {
    items: {},
    observers: {},
    defaultItems: {
      text: {
        value: '',
        type: 'text'
      },
      image: {
        value: {
          src: '',
          alt: ''
        }
      },
      html: {
        type: 'html',
        value: ''
      }
    }
  },
  methods: {
    create (context, items) {
      const content = []

      for (let i = 0; i < items.length; i++) {
        const id = this.$getter('dsUtilities/generateInstanceId')
        const type = items[i].type
        const item = this._generateContent(type, items[i].value)

        this.items[id] = item

        content.push({ id, type })
      }

      return content
    },
    remove (context, { id }) {
      if (this.items[id]) {
        delete this.items[id]
      }
    },
    set (context, item) {
      this.items = { ...this.items, ...item }
    },
    subscribe (context, { id, item }) {
      const content = this._getContent(id)

      if (content) {
        this._setObserver(id, item)
      }
    },
    unsubscribe (context, { id, item }) {
      const observers = this._getObservers(id)
      const index = observers.indexOf(item)

      if (index > -1) {
        observers.splice(index, 1)
      }
    },
    update (context, { id, value }) {
      const content = this._getContent(id)

      if (content) {
        content.value = value

        this._updateObservers(id, value)
      }
    },
    _generateContent (type, value) {
      const content = { ...this.defaultItems[type] }

      if (value) {
        content.value = value
      }

      return content
    },
    _getContent (id) {
      return this.items[id]
    },
    _setObserver (id, item) {
      const observers = this._getObservers(id)
      const content = this._getContent(id)

      if (observers.length) {
        const index = observers.indexOf(item)

        if (index === -1) {
          observers.push(item)
        }
      } else {
        this.observers[id] = [item]
      }

      this._updateObserver(id, item, content.value)
    },
    _getObservers (id) {
      return this.observers[id] || []
    },
    _updateObserver (id, observer, value) {
      const observers = this._getObservers(id)
      const index = observers.indexOf(observer)

      if (index > -1) {
        observers[index].dsContent = value
      }
    },
    _updateObservers (id, value) {
      const observers = this._getObservers(id)

      for (let i = 0; i < observers.length; i++) {
        observers[i].dsContent = value
      }
    }
  }
}
