import { name, version } from '../ds.plugin.config'

export default {
  name,
  version,
  data: {
    params: {},
    usedBy: {},
    instance: {}
  },
  methods: {
    process ({
      id,
      itemId,
      parentItemId,
      paramType,
      params,
      data,
      results,
      variables,
      lastItem,
      debug
    }) {
      if (debug) {
        debugger
      }

      const config = { id, itemId, parentItemId }

      if (!this.instance[id]) {
        this.instance[id] = {
          data: {},
          iteration: {},
          variables: {},
          results: {}
        }
      }

      if (data) {
        this.instance[id].data = data
      }

      if (results) {
        this.instance[id].results[parentItemId] = results
      }

      if (variables) {
        variables = this._processVariables(variables, config)

        this.instance[id].variables = {
          ...this.instance[id].variables,
          ...variables
        }
      }

      const result = this['_data/' + paramType](params, config)

      if (lastItem) {
        delete this.instance[id]
      }

      return result
    },
    setParams ({ id, item, usedBy }) {
      this.params[id] = item
    },
    _getParam (item, instanceId) {
      if (item._$id) {
        return this.params[item._$id]
      }

      return item
    },
    _get (item, config) {
      if (!item._$computed &&
        !item._$computedParams &&
        !item._$id) {
        return item
      }

      item = this._getParam(item, config.id)

      if (item._$computedParams) {
        return this.process({ ...item, ...config })
      }

      return this['_' + item.name](
        this._getParam(item.params, config.id),
        config
      )
    },
    _getValue (item, data) {
      if (Object.hasOwnProperty.call(data, item)) {
        return data[item]
      }

      if (Object.hasOwnProperty.call(item, 'value')) {
        return data
      }

      if (Object.hasOwnProperty.call(item, 'key')) {
        const key = item.key
        const dotNotations = key.split('.')
        let result = { ...data }

        for (let i = 0; i < dotNotations.length; i++) {
          const key = dotNotations[i]

          if (Object.hasOwnProperty.call(data, key)) {
            result = data[key]
          } else {
            return result
          }
        }

        return result
      }

      if (Object.hasOwnProperty.call(item, 'index')) {
        return data[item.index]
      }

      if (Object.hasOwnProperty.call(item, 'indexes')) {
        const result = []

        for (let i = 0; i < item.indexes.length; i++) {
          const index = item[i]

          result.push(data[index])
        }

        return result
      }

      const result = {}

      for (let i = 0; i < item.length; i++) {
        const key = item[i]
        result[key] = data[key]
      }

      return result
    },
    _getVariable (item, config) {
      const variable = this.instance[config.id].variables[item]

      if (variable) {
        if (Array.isArray(variable)) {
          return [...variable]
        } else if (typeof variable === 'object') {
          return { ...variable }
        } else {
          return variable
        }
      }
    },
    _arrayTransform (item, config) {
      const map = item.map
      const flat = item.flat
      let results = []
      let list = item.list

      if (list._$computed) {
        list = this._get(list, config)
      }

      for (let index = 0; index < list.length; index++) {
        const listItem = list[index]
        const mapItems = []

        for (let i = 0; i < map.length; i++) {
          const mapItem = {}

          for (let j = 0; j < map[i].length; j++) {
            const k = map[i][j][0]
            const v = map[i][j][1]

            if (listItem[v]) {
              mapItem[k] = listItem[v]
            } else if (Object.hasOwnProperty.call(v, 'value')) {
              mapItem[k] = v.value
            } else if (Object.hasOwnProperty.call(v, 'key')) {
              const key = v.key
              const dotNotation = key.split('.')
              let result = listItem

              for (let i = 0; i < dotNotation.length; i++) {
                const dKey = dotNotation[i]

                if (result[dKey]) {
                  result = result[dKey]
                }
              }

              mapItem[k] = result
            } else if (Object.hasOwnProperty.call(v, 'index')) {
              mapItem[k] = index
            } else {
              this.instance[config.id].iteration = {
                ...listItem,
                _$index: index
              }

              mapItem[k] = this._get(v, config)
            }
          }

          if (item.onIteration) {
            this.instance[config.id].iteration = {
              ...mapItem,
              _$index: index
            }

            this.$context.method('dsWorkflow/rules', {
              ...item.onIteration,
              id: config.id,
              itemId: config.itemId,
              parentItemId: config.parentItemId
            })
          }

          mapItems.push(mapItem)
        }

        results.push(mapItems)
      }

      if (flat) {
        results = results.flat(flat)
      }

      return results
    },
    _eval (expression, config, result) {
      for (let i = 0; i < expression.length; i++) {
        const item = expression[i]
        const values = this._processValues(item.values, config)

        result += this.$context.method('dsOperators/eval', { name: item.name, values })
      }

      return result
    },
    _evalBoolean (expression, config) {
      return !!this._eval(expression, config, false)
    },
    _evalNumber (expression, config) {
      return this._eval(expression, config, 0)
    },
    _evalString (expression, config) {
      return this._eval(expression, config, '')
    },
    _processValues (values, config) {
      const result = []

      for (let i = 0; i < values.length; i++) {
        const item = values[i]

        if (item.value) {
          result.push(item.value)
        } else {
          result.push(this._get(item, config))
        }
      }

      return result
    },
    _processVariables (variables, config) {
      const result = {}

      for (let i = 0; i < variables.length; i++) {
        const key = variables[i][0]
        const item = variables[i][1]

        if (item.value) {
          result[key] = item.value
        } else {
          result[key] = this._get(item, config)
        }
      }

      return result
    },
    '_getter/app/state' (item, config) {
      let params

      if (item.params) {
        params = this._get(item.params, config)
      }

      const result = this.$context.store.getters[item.name](params)

      if (item.return) {
        return this._getValue(item.return, result)
      }

      return result
    },
    '_getter/plugin/method' (item, config) {
      let params

      if (item.params) {
        params = this._get(this._getParam(item.params, config.id), config)
      }

      const result = this.$context.method(item.name, params)

      if (item.return) {
        return this._getValue(item.return, result)
      }

      return result
    },
    '_getter/this/id' (item, { id }) {
      return this.instance[id].data.id
    },
    '_getter/this/instanceId' (item, { id }) {
      return this.instance[id].data.instanceId
    },
    '_getter/this/value' (item, { id }) {
      const data = this.instance[id].data.value

      if (item) {
        return this._getValue(item, { ...data })
      }

      return data
    },
    '_getter/iteration/value' (item, { id }) {
      const data = this.instance[id].iteration

      if (item) {
        return this._getValue(item, { ...data })
      }

      return data
    },
    '_getter/parent/value' (item, { id, parentItemId }) {
      if (item._$itemId) {
        parentItemId = item._$itemId
      }

      const data = this.instance[id].results[parentItemId]

      if (Object.hasOwnProperty.call(data, item)) {
        return data[item]
      }

      if (Object.hasOwnProperty.call(item, 'value')) {
        return data
      }

      if (Object.hasOwnProperty.call(item, 'key')) {
        const key = item.key
        const dotNotations = key.split('.')
        let result = data

        for (let i = 0; i < dotNotations.length; i++) {
          const key = dotNotations[i]

          if (result[key]) {
            result = result[key]
          } else {
            return result
          }
        }
      }
    },
    '_getter/parent/array' (item, { id, parentItemId }) {
      const result = []

      if (item._$itemId) {
        parentItemId = item._$itemId
      }

      const data = this.instance[id].results[parentItemId]

      for (let i = 0; i < item.indexes.length; i++) {
        const index = item[i]

        result.push(data[index])
      }

      return result
    },
    '_getter/parent/object' (item, { id, parentItemId }) {
      const results = {}

      if (item._$itemId) {
        parentItemId = item._$itemId
      }

      const data = this.instance[id].results[parentItemId]

      for (let i = 0; i < item.keys.length; i++) {
        const key = item[i]
        const dotNotations = key.split('.')
        let result = data

        for (let i = 0; i < dotNotations.length; i++) {
          const key = dotNotations[i]

          if (result[key]) {
            result = result[key]
          }
        }

        results[key] = result
      }

      return results
    },
    '_data/array' (params, config) {
      const result = []

      for (let i = 0; i < params.length; i++) {
        result.push(this._get(params[i], config))
      }

      return result
    },
    '_data/object' (params, config) {
      const result = {}

      for (let i = 0; i < params.length; i++) {
        const item = params[i]

        result[item[0]] = this._get(item[1], config)
      }

      return result
    },
    '_data/value' (params, config) {
      return this._get(params, config)
    }
  }
}
