import { name, version } from '../ds.plugin.config'

/**
 * Dooksa element plugin.
 * @module plugin
 */
export default {
  name,
  version,
  dependencies: [
    {
      name: 'dsEvent',
      version: 1
    }
  ],
  data: {
    items: {
      dsDiv: {
        is: 'ds-div',
        tag: 'div'
      },
      dsButton: {
        is: 'ds-button',
        tag: 'button'
      },
      dsImage: {
        is: 'ds-image',
        tag: 'img'
      },
      dsLabel: {
        is: 'ds-label',
        tag: 'label'
      },
      dsInput: {
        is: 'ds-input',
        tag: 'input'
      },
      dsTextarea: {
        is: 'ds-textarea',
        tag: 'textarea',
        attributes: { rows: '3' }
      },
      get dsText () {
        return new window.DsText()
      },
      get dsFragment () {
        return new window.DocumentFragment()
      }
    }
  },
  setup () {
    const DsElementMixin = (base) => (
      class DsElementMixin extends base {
        constructor () {
          super()

          this.$action = () => {}
        }

        connectedCallback () {
          this.$action('dsEvent/emit', {
            id: this.elementId,
            name: 'mounted'
          })
        }

        disconnectedCallback () {
          this.$action('dsEvent/emit', {
            id: this.elementId,
            name: 'unmounted'
          })
        }
      }
    )

    class DsDivElement extends DsElementMixin(window.HTMLDivElement) {
      get dsContent () {
        return this.innerHTML
      }

      set dsContent (value) {
        this.innerHTML = value
      }
    }

    class DsButtonElement extends DsElementMixin(window.HTMLButtonElement) {
      constructor () {
        super()

        this.addEventListener('click', (e) => {
          this.$action('dsEvent/emit', {
            id: this.elementId,
            name: 'click'
          })
        })
      }

      get dsContent () {
        return this.innerHTML
      }

      set dsContent (value) {
        this.innerHTML = value
      }
    }

    class DsInputElement extends DsElementMixin(window.HTMLInputElement) {
      constructor () {
        super()

        this.addEventListener('input', (e) => {
          this.$action('dsEvent/emit', {
            id: this.elementId,
            name: 'input',
            payload: e.target.value
          })
        })
      }

      get dsContent () {
        return this.value
      }

      set dsContent (value) {
        this.value = value
      }
    }

    class DsLabelElement extends DsElementMixin(window.HTMLLabelElement) {
      get dsContent () {
        return this.innerHTML
      }

      set dsContent (value) {
        this.innerHTML = value
      }
    }

    class DsTextAreaElement extends DsElementMixin(window.HTMLTextAreaElement) {
      constructor () {
        super()

        this.addEventListener('input', (e) => {
          this.$action('dsEvent/emit', {
            id: this.elementId,
            name: 'input',
            payload: e.target.value
          })
        })
      }

      get dsContent () {
        return this.innerText
      }

      set dsContent (value) {
        this.innerText = value
      }
    }

    class DsText extends window.Text {
      get dsContent () {
        return this.textContent
      }

      set dsContent (value) {
        this.textContent = value
      }
    }

    window.DsText = DsText
    window.customElements.define('ds-div', DsDivElement, { extends: 'div' })
    window.customElements.define('ds-button', DsButtonElement, { extends: 'button' })
    window.customElements.define('ds-input', DsInputElement, { extends: 'input' })
    window.customElements.define('ds-label', DsLabelElement, { extends: 'label' })
    window.customElements.define('ds-textarea', DsTextAreaElement, { extends: 'textarea' })
  },
  methods: {
    create (context, item) {
      const component = this._getComponent(item.id)
      const attributes = []

      if (item.classList) {
        attributes.push(['class', item.classList.join(' ')])
      }

      if (item.attributes) {
        component.attributes = { ...component.attributes, ...item.attributes }

        for (const key in component.attributes) {
          attributes.push([key, component.attributes[key]])
        }
      } else if (component.attributes) {
        for (const key in component.attributes) {
          attributes.push([key, component.attributes[key]])
        }
      }

      return this._createHTMLElement(component.tag, component.is, attributes)
    },
    createNode (context, id) {
      return this.items[id]
    },
    _getComponent (id) {
      return { ...this.items[id] }
    },
    _createHTMLElement (tag, is, attributes = []) {
      const element = document.createElement(tag, { is })

      element.$action = this.$action

      for (let i = 0; i < attributes.length; i++) {
        element.setAttribute(attributes[i][0], attributes[i][1])
      }

      return element
    }
  }
}
