import { name, version } from '../ds.plugin.config'

/**
 * Ds Plugin.
 * @module plugin
 */

export default {
  name,
  version,
  dependencies: [
    {
      name: 'dsContent',
      version: 1
    },
    {
      name: 'dsLayout',
      version: 1
    }
  ],
  data: {
    rootElement: null
  },
  setup ({ rootElement, assetsURL }) {
    this.rootElement = rootElement
    // Cached file name for current path
    const pathFilename = window.location.pathname.replace('/', '-') + '.js'
    // Fetch cache json page if available
    const script = new this.$Script({
      id: 'firebase-auth-script',
      src: assetsURL + '/app_cache/' + window.location.hostname + '/' + pathFilename,
      globalVars: ['dsAppCache']
    })

    script.load()
      .then(({ dsAppCache }) => {
        if (dsAppCache.content) {
          this.$method('dsContent/set', dsAppCache.content.items)
        }

        if (dsAppCache.widgets.content) {
          this.$method('dsLayout/setContent', dsAppCache.widgets.content)
        }

        if (dsAppCache.widgets.children) {
          this.$method('dsLayout/setChildren', dsAppCache.widgets.children)
        }

        if (dsAppCache.widgets.layout) {
          this.$method('dsLayout/setLayout', dsAppCache.widgets.layout)
        }

        if (dsAppCache.widgets.baseItems) {
          this.$method('dsLayout/setBaseItems', dsAppCache.widgets.baseItems)
        }

        for (let i = 0; i < 50; i++) {
          const component = this.$method('dsLayout/create', 'layoutDocId_1')
          this.rootElement.appendChild(component)
        }

        rootElement.classList.remove('loading')
      })
      .catch(e => console.error(e))
  },
  methods: {
  }
}
