import { name, version } from '../ds.plugin.config'
// ISSUE: [DS-599] use script loader if nano id are not needed for page rendering
import { customAlphabet } from 'nanoid'

export default {
  name,
  version,
  data: {
    docId: null,
    instanceId: null
  },
  setup ({ docId, instanceId }) {
    this.docId = customAlphabet(docId.alphabet, docId.alphabetLength)
    this.instanceId = customAlphabet(instanceId.alphabet, instanceId.alphabetLength)
  },
  getters: {
    /**
     * Generate firebase UUID
     */
    generateDocId () {
      return this.docId()
    },
    /**
     * Generate ds instance UUID
     */
    generateInstanceId () {
      return this.instanceId()
    }
  }
}
